@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400;500;600;700&family=Montserrat:wght@300;400;500;600;700&display=swap');

body {
    margin: 0;
    font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

@layer base {
  :root {
        /* Primary Backgrounds - White with green accents */
        --bg-primary: #ffffff;
        --bg-secondary: #f8faf9;
        --bg-subtle: #e8f5e9;
        
        /* Text Hierarchy */
        --text-primary: #1a1a1a;
        --text-secondary: #4a4a4a;
        --text-light: #757575;
        --text-meta: #616161;
        
        /* Green Accent Colors */
        --accent-green-dark: #2d5016;
        --accent-green-medium: #4a7c2c;
        --accent-green-light: #6b9d3e;
        --accent-green-subtle: #a8d08d;
        --accent-green-pale: #e8f5e9;
        
        /* Interactive States */
        --interactive-base: #2d5016;
        --interactive-hover: #1a3009;
        --focus-outline: #4a7c2c;
        
        /* Borders & Separators */
        --border-light: #e0e0e0;
        --border-medium: #bdbdbd;
        --border-subtle: rgba(45, 80, 22, 0.1);
        
        /* Status Colors */
        --status-error: #c62828;
        --status-success: #2d5016;
        --status-warning: #f57c00;
        --status-info: #1976d2;

        --background: 0 0% 100%;
        --foreground: 0 0% 3.9%;
        --card: 0 0% 100%;
        --card-foreground: 0 0% 3.9%;
        --popover: 0 0% 100%;
        --popover-foreground: 0 0% 3.9%;
        --primary: 0 0% 9%;
        --primary-foreground: 0 0% 98%;
        --secondary: 0 0% 96.1%;
        --secondary-foreground: 0 0% 9%;
        --muted: 0 0% 96.1%;
        --muted-foreground: 0 0% 45.1%;
        --accent: 0 0% 96.1%;
        --accent-foreground: 0 0% 9%;
        --destructive: 0 84.2% 60.2%;
        --destructive-foreground: 0 0% 98%;
        --border: 0 0% 89.8%;
        --input: 0 0% 89.8%;
        --ring: 0 0% 3.9%;
        --chart-1: 12 76% 61%;
        --chart-2: 173 58% 39%;
        --chart-3: 197 37% 24%;
        --chart-4: 43 74% 66%;
        --chart-5: 27 87% 67%;
        --radius: 0.5rem;
    }
  .dark {
        --background: 0 0% 3.9%;
        --foreground: 0 0% 98%;
        --card: 0 0% 3.9%;
        --card-foreground: 0 0% 98%;
        --popover: 0 0% 3.9%;
        --popover-foreground: 0 0% 98%;
        --primary: 0 0% 98%;
        --primary-foreground: 0 0% 9%;
        --secondary: 0 0% 14.9%;
        --secondary-foreground: 0 0% 98%;
        --muted: 0 0% 14.9%;
        --muted-foreground: 0 0% 63.9%;
        --accent: 0 0% 14.9%;
        --accent-foreground: 0 0% 98%;
        --destructive: 0 62.8% 30.6%;
        --destructive-foreground: 0 0% 98%;
        --border: 0 0% 14.9%;
        --input: 0 0% 14.9%;
        --ring: 0 0% 83.1%;
        --chart-1: 220 70% 50%;
        --chart-2: 160 60% 45%;
        --chart-3: 30 80% 55%;
        --chart-4: 280 65% 60%;
        --chart-5: 340 75% 55%;
    }
}

@layer base {
  * {
    @apply border-border;
    }
  body {
    @apply bg-background text-foreground;
    }
}

/* Typography System - Luxury Minimalist */
.hero-large {
    font-size: 48px;
    font-weight: 300;
    line-height: 1.2;
    font-family: 'Cormorant Garamond', serif;
    color: var(--text-primary);
    letter-spacing: -0.02em;
}

.hero-medium {
    font-size: 36px;
    font-weight: 400;
    line-height: 1.3;
    font-family: 'Cormorant Garamond', serif;
    color: var(--text-primary);
}

.heading-1 {
    font-size: 32px;
    font-weight: 400;
    line-height: 1.2;
    font-family: 'Cormorant Garamond', serif;
    color: var(--text-primary);
}

.heading-2 {
    font-size: 24px;
    font-weight: 500;
    line-height: 1.3;
    font-family: 'Montserrat', sans-serif;
    color: var(--text-primary);
}

.heading-3 {
    font-size: 18px;
    font-weight: 500;
    line-height: 1.4;
    font-family: 'Montserrat', sans-serif;
    color: var(--text-primary);
}

.body-large {
    font-size: 18px;
    font-weight: 400;
    line-height: 1.7;
    font-family: 'Montserrat', sans-serif;
    color: var(--text-primary);
}

.body-regular {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.6;
    font-family: 'Montserrat', sans-serif;
    color: var(--text-primary);
}

.body-small {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.5;
    font-family: 'Montserrat', sans-serif;
    color: var(--text-secondary);
}

.button-text {
    font-size: 14px;
    font-weight: 600;
    line-height: 1.4;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 0.5px;
    text-transform: uppercase;
}

.navigation-text {
    font-size: 14px;
    font-weight: 500;
    line-height: 1.4;
    font-family: 'Montserrat', sans-serif;
    color: var(--text-primary);
}

/* Button Styles - Sharp rectangular with 0px border radius */
.btn-primary {
    background: transparent;
    color: var(--interactive-base);
    border: 2px solid var(--interactive-base);
    border-radius: 0px;
    padding: 16px 32px;
    min-width: 200px;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(.645,.045,.355,1);
    text-decoration: none;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}

.btn-primary:hover {
    background: var(--interactive-base);
    color: #ffffff;
    transform: translateY(-2px);
    box-shadow: 0 8px 24px rgba(45, 80, 22, 0.2);
}

.btn-primary:active {
    background: var(--interactive-hover);
    color: #ffffff;
    transform: translateY(0);
}

.btn-secondary {
    background: none;
    border: none;
    padding: 12px 16px;
    font-size: 14px;
    font-weight: 500;
    font-family: 'Montserrat', sans-serif;
    color: var(--interactive-base);
    cursor: pointer;
    text-decoration: none;
    position: relative;
    transition: all 0.3s ease;
}

.btn-secondary:after {
    content: '';
    position: absolute;
    bottom: 8px;
    left: 16px;
    width: 0;
    height: 2px;
    background: var(--interactive-base);
    transition: width 0.5s ease-in-out;
}

.btn-secondary:hover:after {
    width: calc(100% - 32px);
}

/* Spacing System */
.section-padding {
    padding: 120px 0;
}

.section-padding-small {
    padding: 80px 0;
}

@media (max-width: 1024px) {
    .hero-large {
        font-size: 36px;
    }
    
    .hero-medium {
        font-size: 28px;
    }
    
    .section-padding {
        padding: 80px 0;
    }
    
    .section-padding-small {
        padding: 60px 0;
    }
}

@media (max-width: 640px) {
    .hero-large {
        font-size: 32px;
    }
    
    .hero-medium {
        font-size: 24px;
    }
    
    .section-padding {
        padding: 60px 0;
    }
    
    .section-padding-small {
        padding: 40px 0;
    }
}

/* Smooth transitions */
.transition-smooth {
    transition: all 0.3s cubic-bezier(.645,.045,.355,1);
}

/* Hover lift effect */
.hover-lift {
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.hover-lift:hover {
    transform: translateY(-4px);
    box-shadow: 0 12px 32px rgba(0, 0, 0, 0.1);
}
